import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      // name: 'index',
      component: () => import(/* webpackChunkName: "about" */ './views/Index.vue'),
      children: [
        { path: '', redirect: '/home' },
        { path: '/home', name: 'home', component: () => import('./views/Home.vue') },
        { path: '/order', name: 'order', component: () => import('./views/Order.vue') },
        { path: '/me', name: 'me', component: () => import('./views/Me.vue') },
        { path: '/address', name: 'address', component: () => import('./views/Address.vue') },
        { path: '/city', name: 'city', component: () => import('./views/City.vue') },
        { path: '/search', name: 'search', component: () => import('./views/Search.vue') },
        {
          path: '/shop', redirect:'/goods',name: 'shop', component: () => import('./views/Shops/Shop.vue'),
          children: [
            { path: '/goods', name: 'goods', component: () => import('./views/Shops/Goods.vue') },
            { path: '/comments', name: 'comments', component: () => import('./views/Shops/Comments.vue') },
            { path: '/seller', name: 'seller', component: () => import('./views/Shops/Seller.vue') },
          ],
        }





      ]
    },
    {
      path: '/login',
      name: 'login',
      component: () => import(/* webpackChunkName: "about" */ './views/Login.vue')
    },
    {
      path: '/myAddress',
      name: 'myAddress',
      component: () => import(/* webpackChunkName: "about" */ './views/Orders/MyAddress.vue')
    },
    {
      path: '/addAddress',
      name: 'addAddress',
      component: () => import(/* webpackChunkName: "about" */ './views/Orders/AddAddress.vue')
    },
    {
      path: '/settlement',
      name: 'settlement',
      component: () => import(/* webpackChunkName: "about" */ './views/Orders/Settlement.vue')
    },
    {
      path: '/remark',
      name: 'remark',
      component: () => import(/* webpackChunkName: "about" */ './views/Orders/Remark.vue')
    },
    {
      path: '/pay',
      name: 'pay',
      component: () => import(/* webpackChunkName: "about" */ './views/Orders/Pay.vue')
    },
    {
      path: '/orderInfo',
      name: 'orderInfo',
      component: () => import(/* webpackChunkName: "about" */ './views/Orders/OrderInfo.vue')
    },
  ]
})


router.beforeEach((to, from, next) => {
    const isLogin = localStorage.ele_token ? true : false;
    if (to.path == '/login') {
      next();
    } else {
      // 是否在登录状态下
      isLogin ? next() : next('/login');
    }
  });

