import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const types = {
  SET_LOCATION:'SET_LOCATION',
  SET_ADDRESS:'SET_ADDRESS',
  ORDER_INFO:'ORDER_INFO',//购物车商品进行结算
  USER_INFO:'USER_INFO',//用户的地址
  REMARK_INFO:'REMARK_INFO',//存储用户选择的餐具份数

}

const state = {
  location:{},
  address:'',
  orderInfo:null,//购物车商品进行结算
  userInfo:null,//用户的地址
  remarkInfo: {//存储用户选择的餐具份数
    tableware: '',//餐具分数
    remark: ''//备注信息
  }
}

const getters = {
  location:state => state.location,
  address:state => state.address,
  orderInfo:state => state.orderInfo,//购物车商品进行结算
  userInfo:state => state.userInfo,//用户的地址
  // 结算页面的商品总价
  totalPrice:state => {
    let price = 0;
    if(state.orderInfo){
      const selectFoods = state.orderInfo.selectFoods;
      selectFoods.forEach(food=>{
        price += food.activity.fixed_price * food.count;
      });
      price += state.orderInfo.shopInfo.float_delivery_fee;
    }
    return price;
  },
  remarkInfo:state => state.remarkInfo,//存储用户选择的餐具份数

}

const mutations ={
  [types.SET_LOCATION](state,location){
    // 判断当前的location 是否存在
    if(location){
      state.location = location;
    }else{
      state.location = null;
    }
  },

  [types.SET_ADDRESS](state,address){
    // 判断当前的location 是否存在
    if(address){
      state.address = address;
    }else{
      state.address = null;
    }
  },
//购物车商品进行结算
  [types.ORDER_INFO](state,orderInfo){
    // 判断当前的location 是否存在
    if(orderInfo){
      state.orderInfo = orderInfo;
    }else{
      state.orderInfo = null;
    }
  },

  //用户的地址
  [types.USER_INFO](state,userInfo){
    // 判断当前的location 是否存在
    if(userInfo){
      state.userInfo = userInfo;
    }else{
      state.userInfo = null;
    }
  },

  //存储用户选择的餐具份数
  [types.REMARK_INFO](state,remarkInfo){
    if(remarkInfo){
      state.remarkInfo = remarkInfo;
    }else{
      state.remarkInfo = null;
    }
  },

}



const actions = {
  setLocation:({commit},location) =>{
    commit(types.SET_LOCATION,location);
  },
  setAddress:({commit},address) =>{
    commit(types.SET_ADDRESS,address)
  },
  //购物车商品进行结算
  setOrderInfo:({commit},orderInfo) =>{
    commit(types.ORDER_INFO,orderInfo)
  },
  //用户地址
  setUserInfo:({commit},userInfo) =>{
    commit(types.USER_INFO,userInfo)
  },
  //存储用户选择的餐具份数
  setRemarkInfo:({commit},remarkInfo) =>{
    commit(types.REMARK_INFO,remarkInfo)
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})
